@import url(https://fonts.googleapis.com/css?family=Muli:300,400,500,700,900|Poppins:300,400,500,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,h2,h3,h4,h5,h6{font-family:"Poppins"}.large-title{font-size:4rem;font-weight:700}.description{font-size:1.5em}

.event-card{width:100%;height:100%}.event-card:hover{cursor:pointer}.event-card-content{color:white;position:relative;height:100%}.event-card-background{height:100%;width:100%;position:absolute;top:0%;left:0;bottom:0;right:0;z-index:-1;overflow:hidden;background-color:#13020a;color:#fafafa}.event-card-container{position:relative;height:100%}.grayout{opacity:0.05;filter:alpha(opacity=5)}

.officer-backdrop{background-image:url(/static/media/Officers.b11db321.jpeg);background-position:center;background-attachment:fixed;height:700px;background-size:cover}.background-tint{background-color:rgba(69,6,35,0.55);background-blend-mode:multiply}#slogan{width:45%}@media only screen and (max-width: 768px){#slogan{width:100%}}.title{font-size:4em;font-weight:500 !important}

.navbar{font-family:"Muli";font-size:2em}

.footer{color:white;background:#000;background:-webkit-gradient(linear, left bottom, left top, from(#000), to(#450623));background:linear-gradient(0deg, #000 0%, #450623 100%);height:280px}@media only screen and (max-width: 768px){#footer-socials{padding-top:10%}}a .fab{color:white}

#our-friends{height:100%}.our-friends-section{background-color:#fafafa}.our-friends-picture{width:200px;height:200px}

#source-code-section{background-color:#fafafa}

.history-backdrop{background-image:url(/static/media/historybackground.c1722f73.jpeg);background-position:center;height:20%;background-size:cover;background-color:rgba(69,6,35,0.8);background-blend-mode:multiply}

.about-title{font-family:"Muli";font-weight:700;color:white}.about-info{font-family:"Muli";color:white;font-size:1.4em}

.ourevents-backdrop{background-image:url(/static/media/oureventsbackground.3aa662f3.jpeg);background-position:center;height:20%;background-size:cover;background-color:rgba(69,6,35,0.8);background-blend-mode:multiply}

